export default {
  data() {
    return {
      fileList: [],
      inputCommodity: "",
      inputDescribe: "",
      primaryValue: "",
      secondValue: "",
      primaryClassification: [],
      secondClassification: [],
    };
  },
  methods: {
    handleRemove(file, fileList) {
      let params = new URLSearchParams();
      params.append("name", file.name);
      this.axios
        .post("/file/Delete", params)
        .then((res) => {
          let result = res.data;
          if (!result.status) {
            this.$notify.error({
              title: "错误",
              message: result.message,
              duration: 0,
            });
          }
        })
        .catch((err) => {
          this.$notify.error({
            title: "错误",
            message: err,
            duration: 0,
          });
        });
    },

    handlePreview(file) {
      console.log(file);
    },

    handleSuccess(response, file, fileList) {
      if (response.status) {
        this.fileList = fileList;
        console.log(this.fileList);
      } else {
        this.$notify.error({
          title: "错误",
          message: response.message,
          duration: 0,
        });
      }
    },

    primarySelectChange() {
      let params = new URLSearchParams();
      params.append("primaryClassification", this.primaryValue);
      this.axios
        .post("/getSecondClassificationByPrimaryClassification", params)
        .then((res) => {
          let result = res.data;
          if (result.status) {
            this.secondClassification = result.list;
          } else {
            this.$notify.error({
              title: "错误",
              message: result.message,
              duration: 0,
            });
          }
        })
        .catch((err) => {
          this.$notify.error({
            title: "错误",
            message: err,
            duration: 0,
          });
        });
    },

    handleCommit() {
      let fileNams = [];
      for (const iterator of this.fileList) {
        fileNams.push(iterator.name);
      }
      let params = new URLSearchParams();
      params.append("fileNams", fileNams);
      params.append("name", inputCommodity);
      params.append("primary", primaryValue);
      params.append("second", secondValue);
      params.append("describe", inputDescribe);

      this.axios
        .post("/commodity/Add", params)
        .then((res) => {
          let result = res.data;
          if (result.status) {
            this.inputCommodity = "";
            this.inputDescribe = "";
            this.fileList = [];
            this.$message({
              message: result.message,
              type: "success",
            });
          } else {
            this.$notify.error({
              title: "错误",
              message: result.message,
              duration: 0,
            });
          }
        })
        .catch((err) => {});
    },

    submitUpload() {
      this.$refs.upload.submit();
    },
  },
  created() {
    this.axios
      .get("/primaryClassification/getAll")
      .then((res) => {
        this.primaryClassification = res.data;
      })
      .catch((err) => {
        this.$notify.error({
          title: "错误",
          message: err,
          duration: 0,
        });
      });
  },
};



